import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { ServicedeskComponent } from './servicedesk.component';
import { CreateTicketComponent } from './components/create-ticket/create-ticket.component';
import { MyTicketsComponent } from './components/my-tickets/my-tickets.component';
import { HelpdeskAllTicketsComponent } from 'src/app/shared/components/helpdesk-all-tickets/helpdesk-all-tickets.component';
import { HelpDeskCreateTicketComponent } from 'src/app/shared/components/help-desk-create-ticket/help-desk-create-ticket.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'create-ticket', pathMatch: 'full'},
	{ path: 'create-ticket', component: HelpDeskCreateTicketComponent},
	{ path: 'edit-ticket/:id', component: HelpDeskCreateTicketComponent },
	{ path: 'view-ticket', component: HelpdeskAllTicketsComponent},
	{ path: '**', redirectTo: 'create-ticket', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServicedeskRoutingModule { }
